import { Directive, Input } from '@angular/core';
import {
  AbstractControl, ValidationErrors
} from '@angular/forms';
import { FormFieldBase } from './form-base';

@Directive({})
export class TextareaFieldBase
  extends FormFieldBase {
  @Input() rows: number = 4;
  @Input() spellCheck: boolean = false;
  @Input() autocomplete: string = 'off';
  @Input() autocorrect: string = 'off';
  @Input() autocapitalize: string = 'off';
  @Input() enterkeyhint: string = 'enter';

  constructor() {
    super();
  }

  // Optional: Override validate if textarea needs specific validation
  override validate(control: AbstractControl): ValidationErrors | null {
  // No need to call super.validate()
    const specificErrors: ValidationErrors = {};

    // Add textarea specific validation if needed
    // Example:
    // if (control.value && control.value.length > 500) {
    //   specificErrors['maxLength'] = 'Cannot exceed 500 characters.';
    // }

    return Object.keys(specificErrors).length ? specificErrors : null;
  }

  // --- Template Interaction ---
  onInput(event: Event): void {
    const target = event.target as HTMLTextAreaElement;

    this.updateValue(target.value);
  }

  onBlur(): void {
    this.handleBlur();
  }
}
